.header{
	min-width: 100%;
	margin: 10px auto 20px;
}
.header__logo-image{
	display: inline-flex;
	max-width: 60%;
}

.header__title{
	left: 50%;
	font-size: 50px;
	font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
	color: red;
	transform: rotate(-5deg);
	position:absolute
}
.nav{
	margin-top: 15px;
	background-color: rgb(71, 89, 255, 0.8);
}

.nav__list{
	min-width: 100%;
	display: flex;
	justify-content: space-around;
}

.nav__item{
	display: flex;
	justify-content: center;
	align-items: center;
	height: 30px;
	color: rgb(255, 0, 0);

}
.nav__link{
	color: rgb(255, 255, 255);
	font-size: 18px;
	font-family: Arial, Helvetica, sans-serif;
}

@media (max-width: 780px){
	.header__title{
		font-size: 40px;
	}
	.nav__link{
		font-size: 15px;
	}
}


@media(max-width:450px){
	.header__title{
		left: 25%;
		font-size: 35px;
		margin-top: 15px;
		color: red;
		transform: rotate(-5deg);
		position:absolute
	}
	.header__logo-image{
		display: inline-flex;
		max-width: 70%;
	}
	.nav__link{
		font-size: 12px;
	}
}